/* eslint-disable no-unused-vars */
/* eslint-disable max-len */
import React from 'react';
import URLSearchParams from 'url-search-params';
import { Route } from 'react-router-dom'
import { Label, Panel } from 'react-bootstrap'
import IssueFilter from './IssueFilter.jsx';
import IssueTable from './IssueTable.jsx';
import IssueDetail from './IssueDetail.jsx';
import graphQLFetch from './graphQLFetch.js';
import Toast from './Toast.jsx';

export default class IssueList extends React.Component {
  constructor() {
    super();
    this.state = {
      issues: [],
      toastVisible: false,
      toastMessage: '',
      toastType: 'info',
    };
    this.createIssue = this.createIssue.bind(this);
    this.closeIssue = this.closeIssue.bind(this);
    this.deleteIssue = this.deleteIssue.bind(this);
    this.showError = this.showError.bind(this)
    this.dismissToast = this.dismissToast.bind(this)
  }

  componentDidMount() {
    this.loadData();
  }

  componentDidUpdate(prevProps) {
    const { location: { search: prevSearch } } = prevProps;
    const { location: { search } } = this.props;
    if (prevSearch !== search) {
      this.loadData();
    }
  }

  async loadData() {
    const { location: { search } } = this.props;
    const params = new URLSearchParams(search);
    const vars = {};
    if (params.get('status')) vars.status = params.get('status');

    const effortMin = parseInt(params.get('effortMin'), 10)
    if (!Number.isNaN(effortMin)) vars.effortMin = effortMin
    const effortMax = parseInt(params.get('effortMax'), 10)
    if (!Number.isNaN(effortMax)) vars.effortMax = effortMax

    const query = `query issueList(
      $status: StatusType
      $effortMin: Int
      $effortMax: Int
    ) {
      issueList (
        status: $status
        effortMin: $effortMin
        effortMax: $effortMax
      ) {
        id title status owner
        created effort due
      }
    }`;

    const data = await graphQLFetch(query, vars, this.showError);
    if (data) { // Si la data existe pas on va avoir une erreur en utilisant une methode d'un objet qui existe pas
      this.setState({ issues: data.issueList });
    }
  }

  async createIssue(issue) {
    const query = `mutation issueAdd($issue: IssueInputs!) {
      issueAdd(issue: $issue) {
        id
      }
    }`;

    const data = await graphQLFetch(query, { issue }, this.showError);
    if (data) { // Ça sert à rien de recharger si la data a pas changé donc en pratique ça veut dire si on a pas reçu de data en retour
      this.loadData();
    }
  }

  async closeIssue(index) {
    const query = `
      mutation issueClose($id: Int!) {
        issueUpdate(id: $id, changes: {status: Closed}) {
          id title status owner
          effort created due description
        }
      }
    `
    const { issues } = this.state

    const data = await graphQLFetch(query, { id: issues[index].id }, this.showError)
    if (data) {
      this.setState((prevState) => {
        const newList = [...prevState.issues]
        newList[index] = data.issueUpdate
        return { issues: newList }
      })
    } else {
      this.loadData()
    }
  }

  async deleteIssue(index) {
    const { issues } = this.state
    const { id } = issues[index]
    const query = `
      mutation issueDelete($id: Int!) {
        issueDelete(id: $id)
      }
    `
    const data = await graphQLFetch(query, { id }, this.showError)
    if (data && data.issueDelete) {
      this.setState((prevState) => {
        const { issues: prevList } = prevState
        const newList = [...prevList]
        const { location: { pathname, search }, history } = this.props
        if (pathname === `/issues/${id}`) {
          history.push({ pathname: '/issues', search })
        }
        delete newList[index]
        return ({ issues: newList })
      })
    } else {
      this.loadData()
    }
  }
  // ?Pourquoi utiliser le prevState quand on a acces au state et qu'on sait qu'il a pas changé ?

  showError(message) {
    this.setState({ toastVisible: true, toastMessage: message, toastType: 'danger' })
  }

  dismissToast() {
    this.setState({ toastVisible: false, toastMessage: '', toastType: 'info' })
  }

  render() {
    const newLocal = this.state;
    const {
      issues, toastVisible, toastMessage, toastType,
    } = newLocal;
    const { match } = this.props

    // eslint-disable-next-line react/destructuring-assignment
    const hasFilter = this.props.location.search !== ''

    return (
      <React.Fragment>
        <Panel defaultExpanded={hasFilter}>
          <Panel.Heading>
            <Panel.Title toggle>Filter</Panel.Title>
          </Panel.Heading>
          <Panel.Collapse>
            <Panel.Body>
              <IssueFilter />
            </Panel.Body>
          </Panel.Collapse>
        </Panel>
        <IssueTable issues={issues} closeIssue={this.closeIssue} deleteIssue={this.deleteIssue} />
        <hr />
          <Route path={`${match.path}/:id`} component={IssueDetail} />
        <Toast
          showing={toastVisible}
          bsStyle={toastType}
          onDismiss={this.dismissToast}
        >
          {toastMessage}
        </Toast>
      </React.Fragment>
    );
  }
}
